<template>
  <el-container>
    <el-main v-loading="state.loading">
      <el-row>
        <el-col :span="state.left">
          <el-card>
            <el-form ref="form" :model="state.form" label-width="80px">
              <el-form-item label="id">
                <el-input v-model="state.form.id" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label="名称">
                <el-input v-model="state.form.name" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label="详细描述">
                <el-input
                  v-model="state.form.desc"
                  :disabled="true"
                  type="textarea"
                  :autosize="{ minRows: 1, maxRows: 5 }"
                ></el-input>
              </el-form-item>
            </el-form>
          </el-card>
          <el-card>
            <el-tabs type="border-card">
              <el-tab-pane label="状态列表">
                <el-card>
                  <el-table :data="state.stateView.states" style="width: 100%">
                    <el-table-column
                      v-for="item in state.stateView.heads"
                      :label="item.label"
                      :prop="item.propKey"
                      :key="item.label"
                    >
                    </el-table-column>
                  </el-table>
                </el-card>
              </el-tab-pane>

              <el-tab-pane label="事件列表">
                <el-card>
                  <el-table :data="state.events" style="width: 100%">
                    <el-table-column prop="name" label="name">
                    </el-table-column>
                    <el-table-column prop="label" label="label">
                    </el-table-column>
                    <el-table-column
                      prop="comments"
                      label="comments"
                    ></el-table-column>
                  </el-table>
                </el-card>
              </el-tab-pane>
              <el-tab-pane label="Guard列表">
                <el-card>
                  <el-table :data="state.guards" style="width: 100%">
                    <el-table-column prop="name" label="名称">
                    </el-table-column>
                    <el-table-column prop="desc" label="描述">
                    </el-table-column>
                    <el-table-column
                      prop="guardClass"
                      label="类型"
                    ></el-table-column>
                  </el-table>
                </el-card>
              </el-tab-pane>
              <el-tab-pane label="Action列表">
                <el-card>
                  <el-table :data="state.actions" style="width: 100%">
                    <el-table-column prop="name" label="名称">
                    </el-table-column>
                    <el-table-column prop="desc" label="描述">
                    </el-table-column>
                    <el-table-column
                      prop="actionClass"
                      label="类型"
                    ></el-table-column>
                  </el-table>
                </el-card>
              </el-tab-pane>
            </el-tabs>
          </el-card>
        </el-col>
        <el-col :span="state.right">
          <Uml></Uml>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>
<script>
import services from '../../services/services';
import Uml from './components/Uml'

export default {
  components: { Uml },
  name: "Detail",
  data () {
    return {
      state: this.$store.state.detail,
    };
  },
  created () {
    this.fetchData();
  },
  watch: {
    '$route': 'fetchData'
  },
  computed: {
  },
  methods: {
    fetchData () {
      this.$store.commit('refreshDetail', this.$route.params.id);
    }
  },
};
</script>
<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}
</style>
