<template>
  <el-aside width="240px" style="background-color: rgb(238, 241, 246)">
    <el-menu :default-openeds="['1', '3']">
      <el-submenu index="1">
        <template slot="title"><i class="el-icon-menu"></i>状态机列表</template>
        <template v-for="(value, tag) in machines">
          <el-submenu :key="tag" :index="tag">
            <template slot="title">{{ tag }}</template>
            <template v-for="item in value">
              <el-menu-item :id="item.id" :key="item.id" @click="toMachine">
                {{ item.name }}
              </el-menu-item>
            </template>
          </el-submenu>
        </template>
      </el-submenu>
    </el-menu>
  </el-aside>
</template>
<script>
import services from "../services/services.js"
import router from "../router/index.js"

export default {
  name: 'SideBar',
  data () {
    return {
      machines: {}
    }
  },
  methods: {
    initData (data) {
      this.machines = data;
      const dId = data[Object.keys(data)[0]][0].id;
      console.log(data);
      const id = this.$route.params.id ? this.$route.params.id : dId;
      router.push({ name: "detail", params: { id: id } });
    },
    toMachine (event) {
      router.push({ name: "detail", params: { id: event.$attrs.id } });
    }
  },
  mounted () {
    services.metaList().then(res => this.initData(res))
  }
}
</script>
<style scoped>
.el-aside {
  color: #333;
}
</style>
