<template>
  <el-card>
    <el-form ref="form" label-width="80px">
      <el-row>
        <el-col :span="12">
          <el-row>
            <el-checkbox
              label="Show Internal"
              v-model="state.showInternal"
              @change="onConfig"
            ></el-checkbox>
            <el-checkbox
              label="Show Acion"
              v-model="state.showAction"
              @change="onConfig"
            ></el-checkbox>
            <el-checkbox
              label="Remote Render"
              v-model="state.remoteRender"
              @change="changeRemote"
            ></el-checkbox>
          </el-row>
          <el-row>
            EventMode
            <el-select
              style="width: 120px"
              v-model="state.eventViewMode"
              size="mini"
              placeholder="请选择"
              @change="onConfig"
            >
              <el-option
                v-for="item in state.eventViewModes"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-row>
        </el-col>
        <el-col :span="12">
          <el-button type="primary" size="mini" @click="onMin">
            缩小
          </el-button>
          <el-button type="primary" size="mini" @click="onMax">
            放大
          </el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-image
      v-loading="state.imgLoading"
      id="uml"
      :src="state.url"
      fit="fill"
      :preview-src-list="state.urls"
    />
  </el-card>
</template>
<script>
export default {
  name: "UmlView",
  data () {
    return {
      state: this.$store.state.umlView
    };
  },
  methods: {
    async onConfig () {
      this.$store.commit('refreshUml');
    },
    changeRemote (value) {
		this.state.remoteRender=value;
    },
    onMin () {
      this.$store.commit('onMin');
    },
    onMax () {
      this.$store.commit('onMax');
    }
  }

}
</script>
<style scoped>
.el-button {
  float: right;
  margin-right: 10px;
}
</style>

