import Vue from 'vue'
import Vuex from 'vuex'
import services from "../services/services.js"
Vue.use(Vuex)

async function refreshUmlData (umlView, data) {
    const img = data.umlImg;
	if(umlView.remoteRender){
		umlView.url = `http://www.plantuml.com/plantuml/png/${data.umlCode}`;
	}else{
		umlView.url = data.umlImg;
	}
    umlView.urls = [umlView.url];
    umlView.umlCode = data.umlCode;
    umlView.showInternal = data.showInternal;
    umlView.showAction = data.showAction;
    umlView.eventViewMode = data.eventViewMode;
    umlView.eventViewModes = data.eventViewModes;
}
export default new Vuex.Store({
    state: {
        umlView: {
            url: "none",
            umlCode: "",
            urls: [],
            showInternal: false,
            showAction: true,
            remoteRender: false,
            eventViewMode: 'ALL',
            eventViewModes: ['ALL'],
            imgLoading: false
        },
        detail: {
            form: {
                id: "id",
                name: "name",
                desc: "desc",
            },
            left: 8,
            right: 16,
            stateView: {},
            events: [],
            guards: [],
            actions: [],
            loading: true,
        }
    },
    mutations: {
        async refreshDetail (state, id) {
            const detail = state.detail;
            detail.loading = true;
            const main = await services.machineDetail(id, state.umlView);
            refreshUmlData(state.umlView, main.umlView)
            const events = main.eventDescList;
            detail.form = main.metaInfo;
            detail.guards = main.guardDescList;
            detail.actions = main.actionDescList;
            detail.stateView = main.stateView;
            detail.events = events.map((x) => ({
                name: x.name,
                label: x.label,
                comments: x.comments,
            }));
            detail.loading = false;
        },
        async refreshUml (state) {
			state.umlView.imgLoading=true;
            const data = await services.image(state.detail.form.id, state.umlView)
            refreshUmlData(state.umlView, data);
			state.umlView.imgLoading=false;
        },
        onMin (state) {
            const detail = state.detail;
            if (detail.right <= 6) {
                return;
            }
            detail.right -= 2;
            detail.left += 2;
        },
        onMax (state) {
            const detail = state.detail;
            if (detail.right >= 24) {
                return;
            }
            detail.right += 2;
            detail.left -= 2;
        },
    },
    actions: {
    },
    modules: {
    }
})
