import axios from 'axios'

// axios.defaults.baseURL = 'http://localhost:8080/webjars/statemachine';
axios.defaults.baseURL = 'webjars/statemachine';

export default {
    async remoteUml(umlCode){
        return await axios.get(`http://www.plantuml.com/plantuml/png/${umlCode}`);
    },
    async metaList () {
        var res = await axios.get('/metaInfo/');
        return res.data;
    },
    async states (id) {
        var res = await axios.get(`/${id}/states`);
        return res.data;
    },
    async events (id) {
        var res = await axios.get(`/${id}/events`);
        return res.data;
    },
    async metaDetail (id) {
        var meta = await axios.get(`/metaInfo/${id}`);
        return meta.data;
    },
    async machineDetail (id, viewConfig) {
        var res = await axios.post(`/detail/${id}`, viewConfig);
        return res.data;
    },
    async image (id, umlView) {
        var res = await axios.post(`/${id}/uml/img`, umlView);
        return res.data;
    }
}